<h1 id="mqtt" tabindex="-1"><a class="header-anchor" href="#mqtt" aria-hidden="true">#</a> MQTT</h1>
<table>
<thead>
<tr>
<th>名字</th>
<th>值</th>
<th>流向</th>
<th>描述</th>
</tr>
</thead>
<tbody>
<tr>
<td>CONNECT</td>
<td>1</td>
<td>C-&gt;S</td>
<td>客户端请求与服务端建立连接</td>
</tr>
<tr>
<td>CONNACK</td>
<td>2</td>
<td>S-&gt;C</td>
<td>服务端确认连接建立</td>
</tr>
<tr>
<td>PUBLISH</td>
<td>3</td>
<td>CóS</td>
<td>发布消息</td>
</tr>
<tr>
<td>PUBACK</td>
<td>4</td>
<td>CóS</td>
<td>收到发布消息确认</td>
</tr>
<tr>
<td>PUBREC</td>
<td>5</td>
<td>CóS</td>
<td>发布消息收到</td>
</tr>
<tr>
<td>PUBREL</td>
<td>6</td>
<td>CóS</td>
<td>发布消息释放</td>
</tr>
<tr>
<td>PUBCOMP</td>
<td>7</td>
<td>CóS</td>
<td>发布消息完成</td>
</tr>
<tr>
<td>SUBSCRIBE</td>
<td>8</td>
<td>C-&gt;S</td>
<td>订阅请求</td>
</tr>
<tr>
<td>SUBACK</td>
<td>9</td>
<td>S-&gt;C</td>
<td>订阅确认</td>
</tr>
<tr>
<td>UNSUBSCRIBE</td>
<td>10</td>
<td>C-&gt;S</td>
<td>取消订阅</td>
</tr>
<tr>
<td>UNSUBACK</td>
<td>11</td>
<td>S-&gt;C</td>
<td>取消订阅确认</td>
</tr>
<tr>
<td>PING</td>
<td>12</td>
<td>C-&gt;S</td>
<td>客户端发送PING(连接保活)命令</td>
</tr>
<tr>
<td>PINGRSP</td>
<td>13</td>
<td>S-&gt;C</td>
<td>PING命令回复</td>
</tr>
<tr>
<td>DISCONNECT</td>
<td>14</td>
<td>C-&gt;S</td>
<td>断开连接</td>
</tr>
</tbody>
</table>
